/* @import url('https://fonts.googleapis.com/css2?family=Courier+Prime&display=swap'); */
@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=DM+Sans:opsz,wght@9..40,1000&display=swap');


/* for wide computer screen */
@media (width >= 1550px) {

#mainpage{
    position: absolute;
    /* border: solid white; */
    top: 11%;
    bottom: 0%;
    right: 0%;
    left: 0%;
}

/* ---------------------------picture stuff--------------------------------- */
#pictureoutline {
    position: absolute;
    height: 300px;
    width: 300px;
    background-color: #ffffff;
    border-radius: 50%;
    top: 2%;
    left: 5%;
    animation: 2s slide-right ease-out;
}

#pictureinner {
    position: absolute;
    height: 295px;
    width: 295px;
    background-color: #000000;
    border-radius: 50%;
    top: .79%;
    left: .79%;

}

#me {
    position: inherit;
    height: 95.5%;
    width: 95%;
    top: 2.5%;
    left: 2.5%;
    object-fit: cover;
    border-radius: 50%;
    margin: auto;
}


/* ------------------------Links and buttons------------------------------------------------ */


#linkedin{
    margin: auto;
    top: -5%;
    left: -5%;
    animation: slide-right-linked-buttons 2s ease-out;
}

#linkedinback {
    top: 42%;
    left: 10%;
    position: absolute;
    height: 50px;
    width: 50px;
    margin: auto;
    border-radius: 10%;
}

#gitlabback{
    animation: slide-right-git-buttons 2s ease-out;
    top: 48%;
    left: 10.15%;
    position: absolute;
}


#gitlablogo{
    height: 25px;

}


#linkedinbutton{
height: 35px;
background-color: #00000000;
transition: all .2s ease-in-out;
border: none;
}

#linkedinbutton:hover {
    transform: scale(1.1);
}

#linkedinbutton:active{
    transform: scale(.75)

}

#gitlabbutton {
    height: 40px;
    padding: 5px;
    background-color: #00000000;
    border: none;
    transition: all .2s ease-in-out;
}

#gitlabbutton:hover{
    transform: scale(1.1);
}

#gitlabbutton:active{
    transform: scale(.75)
}

/* ---------------------------paragraph stuff---------------------------- */

#paragraph{
    color: white;
    font-size: 2.5vw;
    font-family: 'Oswald', sans-serif;
    padding: 2%;
    animation: fadeIn 8s;
    letter-spacing: -.1vw;
    text-shadow: 4px 4px 2px rgba(0,0,0,0.6);
}

#paracontainer{
    position: absolute;
    width: 35%;
    left: 60%;
    top: 25%;
}


/* -----------------------------quote-------------------------------------- */

#quotebox {
    position: absolute;
    top: 55%;
    left: 2%;
}

#quote {
    font-size: 1vw;
    color: white;
    font-family: 'DM Sans', sans-serif;
    animation: fadeIn 8s;
}

#author{
    font-size: 1vw;
    color: white;
    font-family: 'DM Sans', sans-serif;
    text-align: right;
    animation: fadeIn 8s;
}


  /* -------------------------animations---------------------------- */


  @keyframes slide-right {
    from {
      margin-left: -100%;
    }
    to {
      margin-left: 0%;
    }
  }


  @keyframes slide-right-linked-buttons {
    from {
      margin-left: -1000%;
    }
    to {
      margin-left: 0%;
    }
  }


  @keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
  }

  @keyframes slide-right-linked-buttons {
    from {
      margin-left: -1000%;
    }
    to {
      margin-left: 0%;
    }
  }


  @keyframes slide-right-git-buttons {
    from {
      margin-left: -200%;
    }
    to {
      margin-left: 0%;
    }
  }
}


