@import url('https://fonts.googleapis.com/css2?family=Galada&display=swap');
@import url('https://fonts.googleapis.com/css2?family=DM+Serif+Display&display=swap');


@media (width >= 1550px) {
    #contactcont{
        height: 55%;
        width: 100%;
        position: absolute;
        /* border: solid white; */
        top: 250%;
        right: 0%;
        left: 0%;
        margin: auto;
        display: flex;
        align-items: top;
        justify-content: center;
    }


    #header{
        color: white;
        font-family: 'Galada', cursive;
        rotate: -5deg;
        font-size: 8vw;
        margin: auto;
    }

    #email{
        text-align: center;
        margin: 20px;
        color: white;
        font-size: 1.2vw;
        font-family: 'DM Serif Display', serif;
        letter-spacing: .02vw;
    }

    #phone{
        text-align: center;
        margin: 20px;
        color: white;
        font-size: 1.2vw;
        font-family: 'DM Serif Display', serif;
        letter-spacing: .02vw;
    }

    #linkedinback1 {
        height: 50px;
        width: 100%;
        text-align: center;
    }

    #linkedinbutton1{
    height: 40px;
    background-color: #00000000;
    transition: .2s ease-in-out;
    border: none;
    }

    #linkedinbutton1:hover {
        transform: scale(1.1);
    }

    #linkedinbutton1:active{
        transform: scale(.75)

    }

}



