#slideshow2 {
    position: absolute;
    top: 3%;
    left: 68.5%;
    right: 0%;
    margin: 0 auto;
    overflow: hidden !important;
    height: 50%;
    width: 27%;
  }

  .slideshowSlider {
    white-space: nowrap;
    transition: ease 1000ms;
  }

  .slide2 {
    display: inline-block;
    height: 20vh;
    width: 100%;
  }

  @media (width < 480px) and (width >= 320px) {
    #slideshow2 {
        position: absolute;
        top: 68.5%;
        left: 0%;
        margin: 0 auto;
        overflow: hidden !important;
        height: 50%;
        width: 90%;
      }

      .slideshowSlider {
        white-space: nowrap;
        transition: ease 1000ms;
      }

      .slide2 {
        display: inline-block;
        height: 20vh;
        width: 100%;
      }


  }
