@import url('https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Neuton:ital@1&display=swap');
@import url('https://fonts.googleapis.com/css2?family=DM+Sans:opsz,wght@9..40,1000&display=swap');



html{scroll-behavior:smooth}



/* for wide computer screen */
@media (width >= 1550px) {

#slider {
    position: absolute;
    background: rgba(249, 251, 251, 0);
    height: 10vh;
    border-bottom: .25vh solid rgba(255, 255, 255, 0.861);
    width: 90%;
    top: 0%;
    left: 5%;
}

#navcont {
    position: absolute;
    bottom: 10%;
    left: -1%;
}

#navgrid {
    width: 90vw;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: 1fr;
}

#rightname {
    text-shadow: .2vw .2vw .5vw black;
    margin: auto;
    color: white;
    grid-column: 1/2;
    font-size: 3.5vh;
    font-family: 'Great Vibes', cursive;
}

#projectlink {
    text-shadow: .2vw .2vw .5vw black;
    margin: auto;
    color: white;
    grid-column: 5/6;
    font-size: 2.5vh;
    font-family: 'Neuton', serif;
}

#title {
    position: absolute;
    left: 17.9%;
    top: 35%;
    font-size: .6vw;
    margin: auto;
    color: white;
    grid-column: 2/3;
    grid-row: 1/2;
    font-family: 'DM Sans', sans-serif;
}


#tag{
    text-decoration: none;
    color: white;
    margin: auto;
    padding: 2px;
}


#tag:hover{
    border-bottom: 1px solid white;
}

}


/* ------------------laptop--screens-------------------- */

@media (width < 1550px) and (width >= 768px) {
    #slider {
        position: absolute;
        background: rgba(249, 251, 251, 0);
        height: 9vh;
        border-bottom: .25vh solid rgba(255, 255, 255, 0.861);
        width: 90%;
        top: 0%;
        left: 5%;
    }

    #navcont {
        position: absolute;
        bottom: 10%;
        left: -1%;
    }

    #navgrid {
        margin: auto;
        position: absolute;
        bottom: 110%;
        height: 13vh;
        width: 90vw;
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        grid-template-rows: 1fr;
    }

    #rightname {
        position: relative;
        margin: auto;
        top: 25%;
        left: 10%;
        text-shadow: .2vw .2vw .5vw black;
        color: white;
        grid-column: 1/2;
        font-size: 4.5vh;
        font-family: 'Great Vibes', cursive;
    }

    #projectlink {
        text-shadow: .2vw .2vw .5vw black;
        margin: auto;
        color: white;
        grid-column: 5/6;
        font-size: 3vh;
        position: relative;
        top: 20%;
        font-family: 'Neuton', serif;
    }

    #title {
        position: relative;
        left: 3%;
        top: 25%;
        font-size: .8vw;
        margin: auto;
        color: white;
        grid-column: 2/3;
        grid-row: 1/2;
        font-family: 'DM Sans', sans-serif;
    }


    #tag{
        margin: auto;
        color: white;
        text-decoration: none;

    }


    #tag:hover{
        color: rgb(204, 204, 204);
    }

}


@media (width < 480px) and (width >= 320px) {

    #rightname {
        position: relative;
        margin: auto;
        top: 120%;
        left: 3%;
        text-shadow: .2vw .2vw .5vw black;
        color: white;
        font-family: 'Great Vibes', cursive;
        font-size: 12vw;
    }

    #title {
        position: absolute;
        left: 21%;
        top: 190%;
        font-size: 3vw;
        margin: auto;
        color: white;
        font-family: 'DM Sans', sans-serif;
    }

    #slider {
        position: absolute;
        background: rgba(249, 251, 251, 0);
        height: 9vh;
        border-bottom: .25vh solid rgba(255, 255, 255, 0.861);
        width: 90%;
        top: 0%;
        left: 5%;
    }

    #navcont {
        position: absolute;
        bottom: 10%;
        left: -1%;
    }

    #navgrid {
        margin: auto;
        position: absolute;
        bottom: 100%;
        height: 13vh;
        width: 90vw;
    }


    #projectlink {
        color: white;
        font-size: 0vh;
        position: relative;
        top: -200%;
        font-family: 'Neuton', serif;
        text-align: center;
    }



    #tag{
        color: white;
        text-decoration: none;

    }


    #tag:hover{
        color: rgb(204, 204, 204);
    }


}
