#slideshow {
    position: absolute;
    top: 3%;
    left: 4%;
    margin: 0 auto;
    overflow: hidden !important;
    height: 50%;
    width: 24%;
  }

  #slideshowSlider {
    white-space: nowrap;
    transition: ease 1000ms;
  }

  #slide {
    display: inline-block;
    height: 20vh;
    width: 100%;
  }


  @media (width < 480px) and (width >= 320px) {
    #slideshow {
        position: absolute;
        top: 2%;
        left: 5%;
        margin: 0 auto;
        overflow: hidden !important;
        height: 15%;
        width: 90%;
      }

      #slideshowSlider {
        white-space: nowrap;
        transition: ease 1000ms;
      }

      #slide {
        display: inline-block;
        height: 20vh;
        width: 100%;
      }
  }
