
@media (width >= 1550px) {

#con {
  background-image: url("https://upload.wikimedia.org/wikipedia/commons/thumb/f/ff/The_Inspiration_of_Saint_Matthew-Caravaggio_%281602%29.jpg/1200px-The_Inspiration_of_Saint_Matthew-Caravaggio_%281602%29.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  top: 0%;
  height: 200vh;
  width: 100%;
  margin: 0px;
}

#con::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 200vh;
  background-color: rgba(0, 0, 0, 0.228);
}
}

/* ----------------laptop--screen------------------- */
@media (width < 1550px) and (width >= 768px) {
  #con {
    background-image: url("https://upload.wikimedia.org/wikipedia/commons/thumb/f/ff/The_Inspiration_of_Saint_Matthew-Caravaggio_%281602%29.jpg/1200px-The_Inspiration_of_Saint_Matthew-Caravaggio_%281602%29.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    position: absolute;
    top: 0%;
    height: 200vh;
    width: 99%;
    margin: 0px;
  }
  #con::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 200vh;
    margin: 0px;
    background-color: rgba(0, 0, 0, 0.228);
  }
}


