}

#cardtwo {
    grid-area: 1 / 2 / 2 / 3;
    background-color: rgba(255, 255, 255, 0.438);
    border: solid rgba(0, 0, 0, 0.55);
    border-radius: 10%;
    box-shadow: -1px 58px 29px 1px rgba(0,0,0,0.72);
    -webkit-box-shadow: -1px 58px 29px 1px rgba(0,0,0,0.72);
    -moz-box-shadow: -1px 58px 29px 1px rgba(0,0,0,0.72);
}

#cardthree {
    grid-area: 1 / 3 / 2 / 4;
    background-color: rgba(255, 255, 255, 0.438);
    border: solid rgba(0, 0, 0, 0.55);
    border-radius: 10%;
    box-shadow: -28px 58px 29px 1px rgba(0,0,0,0.72);
    -webkit-box-shadow: -20px 58px 29px 1px rgba(0,0,0,0.72);
    -moz-box-shadow: -28px 58px 29px 1px rgba(0,0,0,0.72);
}

}



