
@media (width >= 1550px) {

#filigreecon{
    height: 40%;
    width: 100%;
    position: absolute;
    /* border: solid white; */
    top: 210%;
    right: 0%;
    left: 0%;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
}


#filigree{
    margin: auto;
    height: 300px;
}
}

@media (width < 1550px) and (width >= 768px) {
    #filigreecon{
        height: 20%;
        width: 100%;
        position: absolute;
        /* border: solid white; */
        top: 105%;
        right: 0%;
        left: 0%;
        margin: auto;
        display: flex;
        align-items: center;
        justify-content: center;
    }


    #filigree{
        margin: auto;
        height: 200px;
    }
}

@media (width < 480px) and (width >= 320px) {
    #filigreecon{
        height: 12%;
        width: 100%;
        position: absolute;
        /* border: solid white; */
        top: 58%;
        right: 0%;
        left: 0%;
        margin: auto;
        display: flex;
        align-items: center;
        justify-content: center;
    }


    #filigree{
        margin: auto;
        height: 150px;
    }

}
